@import 'variable';

.space-between {
	justify-content: space-between;
}


// Message Page
.chat-messagepage-zone {
	position: absolute;
	width: 100%;
	background-color: white;
	height: 100%;
	z-index: 100;
	top: 64px;
	transition: right 0.3s ease-in-out;


	////
	/// Header Zone
	.message-page-header {
		box-shadow: rgb(0 0 0 / 15%) 1.95px 1.95px 2.6px;
		height: 42px;
		position: relative;
		top: 15px;

		&.linetoken {
			height: 44px !important;
		}

		.header-wrapper {

			.back-step-zone {
				width: 11%;

				.backstep-image {
					cursor: pointer;
					position: relative;
					left: 6px;
					top: 5px;
				}

			}

			.image-zone {
				width: 19%;

				.chat-people-header {
					position: relative;
					top: -8px;
				}

			}

			.profile-name {
				width: 50%;

				.message-page-header-username {
					font-weight: bold;
				}

			}

			.option-zone {
				width: 18%;
				position: relative;
				top: -11px;

				.search-history-icon {
					cursor: pointer;
					position: relative;
					top: 5px;
				}

				.message-page-fishdot {
					cursor: pointer;
					position: relative;
					top: -3px;
					left: 5px;
				}

			}

		}

		&.linetoken {
			box-shadow: rgb(0 0 0 / 15%) 1.95px 1.95px 2.6px;
			position: relative;
			top: 0px;
			background-color: #87a6d4;
			z-index: 1;
			padding-bottom: 7px;

			.search-history-zone {
				position: relative;
				top: 9px;
			}

			.chat-people-header {
				display: none;
			}

			.backstep-image {
				cursor: pointer;
				position: relative;
				left: 6px;
				top: 5px !important;
			}

			.profile-name {
				width: 69%;
				padding-top: 3px;
			}

			.option-zone {
				width: 18%;
				position: relative;
				top: -7px;
			}

		}

		.contactlist {
			position: absolute;
			width: 162px;
			right: 9px;
			background-color: white;
			z-index: 100;
			box-shadow: 4px 4px 7px rgb(0 0 0 / 7%), 6px 6px 36px rgb(0 0 0 / 6%);
			border-radius: 6px;
			font-size: 16px;
			display: none;

			&.show {
				display: block;
			}

			ul {
				list-style-type: none;
				margin: 0;
				padding: 0;
			}

			li {
				padding-top: 10px;
				padding-bottom: 12px;
				padding-left: 41px;
				position: relative;
				border-radius: 6px;
				cursor: pointer;
				font-size: 14px;

				img {
					position: absolute;
					left: 14px;
					top: 11px;
				}

			}

			li:hover {
				background-color: #f3f3f3;
			}

		}

	}

	////
	/// Content Zone

	.message-page-content {
		position: relative;
		height: 78%;

		.chat-message-zone {
			height: 333px;
			overflow: scroll;
			position: relative;
			top: 16px;

			.share-tickzone {

				img {
					position: relative;
					left: -10px;
					top: 12px;
				}

			}

			.send-chat-message {
				padding-left: 18px;
				padding-top: 6px;
				position: relative;
			}

			.send-chat-message-content {
				font-weight: 100;
				font-size: 12px;
				background: #ebe3e3;
				border-radius: 15px;
				max-width: 126px;
				padding: 11px;
				line-height: 18px;
				margin-left: 8px;

				&.active-tooltip {
					background: #a19fa0;
				}

				.reply-tooltip {
					width: 35px;
					font-size: 9px;

					img {
						position: relative;
						left: 3px;
					}

					.replytooltip-text {
						font-size: 8px;
					}

				}

				.share-tooltip {
					font-size: 9px;
					position: relative;
					left: 3px;

					img {
						position: relative;
						top: -1px;
						left: 4px;
					}

					.share-tooltiptext {
						width: 35px;
						position: relative;
						top: -2px;
					}

				}

				.separation-line {
					height: 24px;
					position: absolute;
					border-style: solid;
					border-width: 0px 1px 0px 0px;
					width: 10px;
					border-color: #ddd9d936;
					top: 7px;
					left: 34px;
				}

			}

			.send-chat-message-time {
				color: rgba(179, 180, 182, 0.9);
				font-size: 10px;

				span {
					position: absolute;
					bottom: 0;
					margin-left: 6px;
				}

			}

			ul {
				list-style-type: none;
				padding: 0;
				padding-bottom: 33px;
			}

			.day-indicator {

				padding: 15px 0px 3px 0px;

				span {
					color: white;
					background: rgba(48, 62, 87, 0.3);
					border-radius: 40px;
					font-size: 10px;
					font-weight: inherit;
					padding: 2px 10px 2px 9px;
					position: relative;
					left: 121px;
				}

			}

			.recieve-chat-message {

				position: relative;

				div:first-child {
					.tickzone {
						position: absolute;
						left: 8px;
					}
				}

				.recieve-chat-message-content {
					font-weight: 100;
					font-size: 12px;
					background: #3981F7;
					border-radius: 23px;
					color: white;
					max-width: 148px;
					padding: 9px 9px 9px 13px;
					position: relative;
					margin-bottom: 11px;
					margin-top: 15px;
					margin-right: 7px;

					&.replied-include {
						border-radius: 13px;

						.replied-name {
							font-size: 10px;
							margin-left: 6px;
						}

						.replied-message-text {
							font-size: 10px;
							font-weight: revert;
							margin-left: 6px;
						}

						.replied-separation {
							width: 114%;
							height: 2px;
							border-style: solid;
							border-width: 0px 0px 1px 0px;
							border-color: #7f7f91;
							position: relative;
							left: -13px;
							top: 3px;
						}

						.message-text {
							margin-top: 6px;
							margin-bottom: -4px;
						}

					}

				}

				.recieve-chat-message-time {
					color: rgba(179, 180, 182, 0.9);
					font-size: 10px;
					align-self: self-end;
					margin-bottom: 8px;
					margin-right: 9px;
				}


			}

			&.history-offset {
				height: 350px;
			}

		}

		&.linetoken {
			background-color: #8CABD9;

			.recieve-chat-message-content {
				background-color: #9DE694 !important;
				color: black !important;
				border-radius: 15px;
			}

			.replied-separation {
				width: 117% !important;
				border-color: #b8b8cc !important;
			}

			.chat-message-zone {
				top: 0px;
				padding-bottom: 12px;

				&.history-offset {
					height: 357px;
				}

			}

		}

		.tooltip {
			position: relative;
			display: inline-block;
			border-bottom: 0px none black;
		}

		.tooltip .tooltiptext {
			visibility: visible;
			width: 79px;
			background-color: black;
			color: #fff;
			text-align: center;
			border-radius: 6px;
			padding: 3px 7px 1px 2px;
			position: absolute;
			z-index: 1;
			bottom: 150%;
			left: 50%;
			margin-left: -15px;
			top: 62px;
			height: -moz-fit-content;
			height: fit-content;
		}

		.tooltip .tooltiptext::after {
			content: "";
			position: absolute;
			bottom: 100%;
			left: 50%;
			margin-left: -5px;
			border-width: 5px;
			border-style: solid;
			border-color: transparent transparent black transparent;
		}

		.tooltip:hover .tooltiptext {
			visibility: visible;
		}

	}


	////
	/// Footer Zone

	.message-page-footerzone {
		height: fit-content;
		position: absolute;
		bottom: 0;
		background-color: white;
		width: 100%;
		padding-top: 21px;


		.message-footer-addimage {
			position: relative;
			top: 0px;
			width: 30%;

			.chatmessage-imageadd {
				position: absolute;
				cursor: pointer;
				left: 11px;
			}

			.chatmessage-imageicon {
				position: absolute;
				cursor: pointer;
				left: 43px;
			}

		}

		.line_pic_add {
			position: absolute;
			left: 4px;
			top: -6px;
			display: none;
		}

		.text-indi {
			position: absolute;
			left: 5px;
			top: -9px;
			display: none;
			cursor: pointer;
		}

		.emoji-image {
			position: absolute;
			right: 10px;
			bottom: 8px;
			top: 5px;
			cursor: pointer;
		}

		.message-footer-textmessagebox {
			width: 70%;
			position: relative;
			top: -7px;
			left: -8px;
			background: #f5f5f5;
			border: 1px solid #CDCFD7;
			border-radius: 20px;
			float: right;
			transition: width 0.2s ease-in-out;

			textarea {
				border-style: none;
				background-color: transparent;
				position: relative;
				left: 13px;
				top: 4px;
				resize: none;
				outline: none;
				font-size: 14px;
				width: 173px;
				height: 22px;
			}

			::placeholder {
				color: rgb(186, 186, 193);
			}
		}

		&.linetoken {

			.line_pic_add {
				display: block;
				top: -12px;
			}

			.chatmessage-imageadd {
				display: none;
			}

			.message-footer-textmessagebox {
				top: -11px !important;
			}

			.chatmessage-imageicon {
				top: -5px;
			}

		}

		.add-more-options {

			.add-more-options-wrapper {
				width: 49px;
				display: none;

				.options-pics {
					height: 34px;
					margin: auto;
				}

				.options-name {
					font-size: 11px;
					color: #b8b9ba;
				}

			}

			&.show {
				margin-top: 3px;
				margin-left: 12px;
				margin-bottom: 8px;

				.add-more-options-wrapper {
					display: block;
				}
			}

		}

		&.history-offset {
			padding-top: 0;
		}

	}

}

.replied-message-zone {
	position: absolute;
	width: 100%;
	height: -moz-fit-content;
	height: fit-content;
	bottom: 60px;
	background-color: white;
	visibility: hidden;
	padding-top: 7px;

	&.show {
		visibility: visible;
	}

	.replied-message-zone-profilepic {
		margin-left: 12px;
		margin-top: 7px;
		margin-right: 10px;
	}

	.replied-name {
		font-size: 13px;
		font-weight: 500;
		margin-top: 5px;
	}

	.replied-message-text {
		font-size: 13px;
		color: #8f9191;
	}

	.cursor-pointer {
		position: absolute;
		top: 7px;
		right: 8px;
	}

}

.sharebutton-zone {
	position: absolute;
	bottom: 4px;
	background-color: white;
	z-index: 101;
	width: 100%;
	height: 54px;

	&.show {
		bottom: 63px;
	}

	button {
		background: #1877F2;
		border-radius: 8px;
		border-style: none;
		width: 274px;
		height: 36px;
		color: white;
		margin-top: 5px;
	}

	.close-shared-zone {
		position: absolute;
		right: 3px;
		top: -5px;
	}

	&.linetoken {
		height: 62px;
		bottom: -1px;

		button {
			margin-top: 8px;
			background-color: #{$lineColor};
		}

		.close-shared-zone {
			top: -3px;
		}

	}

}

.search-history-zone {

	.myInput {
		border-style: solid;
		border-color: #ebe1e1;
		border-width: 1px;
		width: 249px;
		height: 33px;
		background: #F3F3F3;
		opacity: 0.6;
		border-radius: 6px;

		input {
			border-radius: 10px;
			height: 29px;
			border-width: 0;
			outline: none;
			padding-left: 27px;
			width: 100%;
			background: transparent;
		}

		img {
			position: absolute;
			left: 39px;
			top: 8px;
		}

	}

	.cross-image {
		position: relative;
		top: 6px;
		left: 5px;
	}

	&.linetoken {
		position: relative;
		top: 11px;
		z-index: 10;
		left: -3px;

		.cross-image {
			position: relative;
			top: 7px;
			left: 7px;
		}

	}

}

.history-date-bar {
	width: 100%;
	padding-top: 8px;

	.view-find-zone {
		width: 20%;
		position: relative;
		left: 4px;

		.goup-image {
			transform: rotate(90deg);
			margin-right: 17px;
		}

		.godown-image {
			transform: rotate(-90deg);
			position: relative;
			top: -3px;
		}
	}

	.date-result {
		width: 60%;
		font-size: 12px;
		text-align: center;
		margin-right: 7px;
		margin-top: 6px;
	}

	.calendar-icon {
		width: 20%;

		img {
			position: relative;
			left: 20px;
		}

	}

	.calendar-zone {
		width: 100%;
		position: absolute;
		top: -315px;
		height: 200px;
	}

	&.linetoken {
		padding-bottom: 6px;

		.calendar-zone {
			top: -307px;
		}

	}

}