.side-sharepane-zone {
  width: 100%;
  background-color: white;
  position: absolute;
  top: 0;
  height: 100%;
  left: 300px;
  z-index: 102;
  transition: left 0.3s ease-in;

  &.show {
    left: 0;
  }

  .side-sharepane-header {
    padding: 7px 10px 0px 4px;
    box-shadow: rgb(0 0 0 / 15%) 1.95px 1.95px 2.6px;
  }

  .sidebar-sharelinkbutton {
    color: #1877F2;
    ;
  }

  .search-box {
    margin-top: 11px;
    margin-left: 9px;
  }

  .side-sharepane-content {

    .mytextarea {
      padding: 11px;

      textarea {
        border-style: none;
        width: 100%;
        outline: none;
        resize: none;
      }

    }

    ul {
      margin: 0;
      padding: 0;
      list-style-type: none;

      li {
        margin-top: 9px;
        margin-left: 16px;
        margin-right: 9px;
        position: relative;

        .friend-name {
          position: relative;
          right: -10px;
          top: 7px;
          font-size: 13px;
        }

        .friend-tickzone {
          position: absolute;
          right: 16px;
          top: 5px;
        }

      }

    }

  }

}

.success-share-alert {
  position: absolute;
  top: 108px;
  width: 201px;
  height: 188px;
  background-color: white;
  box-shadow: 4px 4px 7px rgb(0 0 0 / 7%), 6px 6px 36px rgb(0 0 0 / 6%);
  border-radius: 10px;
  left: 49px;
  padding-bottom: 9px;

  .imagesucess-popup {
    position: relative;
    left: 18px;
  }

  .success-popup-title {
    text-align: center;
  }

  .success-popup-description {
    text-align: center;
    font-size: 12px;
    font-weight: 100;
  }

}