.share-panezone {
  position: absolute;
  bottom: -158px;
  background-color: white;
  height: 204px;
  //height: 276px;
  z-index: 102;
  width: 90%;
  margin-left: 0px;
  padding: 19px 17px 0px 15px;
  border-radius: 10px 10px 0px 0px;

  &.show {
    bottom: 67px;

    &.show-messagepane {
      height: 275px;
    }

  }

  .share-panezone-content {

    margin-top: 16px;

    ul {
      margin: 0;
      padding: 0;
      row-gap: 10px;

      li {
        width: 64px;
        position: relative;

        .profile-pic-image {
          margin: auto;
          margin-top: 0;
          margin-bottom: 0;
        }

        .profile-pic-name {
          font-size: 10px;
          text-align: center;
        }

        &.addmorezone {
          position: relative;
          top: -7px;
        }

        .profile-tick-image {
          position: absolute;
          right: 0;
          top: 20px;
        }

      }

    }

  }

  .share-message-input {
    width: 112%;
    height: 54px;
    overflow: scroll;
    border-style: solid;
    border-width: 1px 0px 0px 0px;
    margin: 12px 0px 0px 17px;
    position: relative;
    left: -31px;
    padding-left: 14px;
    border-color: #e2ebeb;
    padding-top: 16px;

    textarea {
      outline: none;
      border-style: none;
      font-size: 12px;
      font-weight: 100;
      width: 93%;
      height: 50px;

      ::placeholder {
        color: red;
      }

    }

  }

  .sharelink {
    color: #1877F2;
  }

}