@import 'variable';

.add-new-contact {
  height: 100%;
  width: 100%;
  background-color: white;
  position: absolute;
  top: 0;
  overflow: scroll;
  left: 300px;
  transition: left 0.3s ease-in;

  &.show {
    left: 0;
    z-index: 100;
  }

  .header {
    justify-content: space-between;
    font-size: 16px;
    padding-top: 10px;
    padding-left: 9px;

    .title {
      position: relative;
      left: -11px;
      top: -2px;
    }

  }

  .form {
    margin: 20px 20px 1px 20px;
    font-size: 13px;

    .required {
      color: red;
    }

    input {
      width: 92%;
      border: 1px solid #EDEDED;
      border-radius: 8px;
      outline: none;
      padding: 8px 7px 7px 9px;
      margin-bottom: 9px;

    }

    input::placeholder {
      color: rgb(223, 217, 217);
    }

    select {
      width: 100%;
      border: 1px solid #EDEDED;
      border-radius: 8px;
      outline: none;
      margin-bottom: 5px;
      height: 37px;
      padding-left: 4px;
    }

    option:not(:first-of-type) {
      color: red;
    }

    .submitbutton {
      background-color: #f0f0f0;
      border-radius: 8px;
      padding-left: 22px;
      padding-top: 4px;
      cursor: pointer;
      height: 40px;

      input {
        color: #c9c3c3;
        cursor: pointer;
      }

      &.active {
        cursor: pointer;

        background-color: #1877F2;

        input {
          background-color: #1877F2;
          border-style: none;
          color: white;
        }

        &.linetoken {
          background-color: $lineColor;

          input {
            background-color: $lineColor;
            border-style: none;
            color: white;
          }

        }

      }

    }

  }



}